.div1 {
  width: 2307px;
  height: 667px;
}
.div1 .titleNode {
  width: 384px;
  height: 120px;
  /* float: left; */
  line-height: 103px;
  text-align: center;
  cursor: pointer;
  position: relative;
  /* margin-left: 88px; */
  font-size: 44px;
  text-shadow: 0 0 30px #f4faf5;
  color: #fff;
}
.div1 .realtime {
  width: 2307px;
  height: 535px;
  border-image-repeat: stretch;
}
.div1 .realtime .smalltable {
  width: 2225px;
  height: 451px;
  overflow: hidden;
}
.div1 .realtime .smalltable .table {
  width: 100%;
  padding-top: 50px;
  padding-left: 60px;
  margin: 44px 0 0 46px;
}
.div1 .realtime .smalltable .table th {
  height: 90px;
  border-top: none;
  border-bottom: 0px solid red;
  font-weight: bold;
  line-height: 1;
  font-size: 40px;
  padding: 16px 8px;
  color: white;
  text-align: center;
}
.div1 .realtime .smalltable .table td {
  height: 81px;
  border-top: none;
  border-bottom: 0px solid red;
  line-height: 1;
  font-size: 40px;
  padding: 16px 8px;
  color: white;
  text-align: center;
}
.div1 .realtime .smalltable .table .striped1 {
  background-color: #172362;
}
.div1 .realtime .smalltable .table .striped {
  background-color: #0a5bb2;
}
.div1 .realtime .smalltable .table .striped2 {
  background-color: #133884;
}
